.search {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #323232;
}
.search label {
  font-size: 1.2rem;
  text-align: center;
  font-weight: bold;
}

.search input {
  padding: 0.6rem 1rem;
  width: 60%;
  margin: 0 1rem;
}

.search button {
  padding: 0.6rem 1rem;
  font: inherit;
  font-weight: bold;
  color: #eaeaea;
  background-color: #265454;
  border: 1px solid #265454;
  border-radius: 4px;
}

.search button:hover,
search button:active {
  cursor: pointer;
  background-color: #1d3f3f;
  border: 1px solid #1d3f3f;
  color: #eaeaea;
}

@media (max-width: 600px) {
  .search {
    display: block;
    text-align: center;
    width: 100%;
    justify-content: center;
  }

  .search input {
    display: block;
    width: 100%;
    margin: 1rem 0;
  }
}
